"use client"

import { InputAdornment, InputLabel, TextField, TextFieldProps } from "@mui/material";
import { Controller, UseFormReturn } from "react-hook-form";

// Extend with MUI props for flexibility, omitting conflicts
interface CustomTextFieldProps extends Omit<TextFieldProps, "variant" | "size" | "rows" | "maxRows"> {
  placeholder?: string;
  size?: "medium" | "small"; // medium is default
  variant?: "outlined" | "filled" | "standard"; // outlined is default
  label?: string;
  disabled?: boolean;
  IconComponent?: React.ElementType;
  iconPosition?: "start" | "end";
  fullWidth?: boolean;
  borderColor?: string; // border color
  textColor?: string;
  bgColor?: string;
  form: UseFormReturn<any>;
  inputTitle?: string;
  formFieldName: string; // name of formValue
  useLabel: boolean;
  multiline?: boolean; // Explicit for textarea mode
  rows?: number; // Renamed to match MUI
  maxRows?: number; // Renamed to match MUI
  multilineLabelOffset?: number; // New: px to adjust shrunk label position (higher number = more space below label)
  fontSize?: number; // the font size of content
  labelFontSize?: number; //the font size of label
}

export default function CustomTextField({
  placeholder,
  size = "medium",
  variant = "outlined",
  label,
  IconComponent,
  iconPosition = "start",
  fullWidth = true,
  borderColor,
  textColor,
  bgColor,
  form,
  inputTitle,
  formFieldName,
  useLabel,
  multiline: explicitMultiline,
  rows,
  maxRows,
  multilineLabelOffset = 15,
  fontSize = 20,
  labelFontSize= 20,
  ...rest // Spread remaining TextFieldProps
}: CustomTextFieldProps) {
  const isMultiline = explicitMultiline || !!rows || !!maxRows;

  return (
    <>
      <Controller
        name={formFieldName}
        control={form.control}
        render={({ field, fieldState: { error } }) => (
          <>
            {inputTitle && <InputLabel>{inputTitle}</InputLabel>}
            <TextField
              {...field}
              {...rest}
              {...(useLabel && { label })}
              placeholder={placeholder}
              variant={variant}
              size={size}
              fullWidth={fullWidth}
              error={!!error}
              helperText={error?.message}
              multiline={isMultiline}
              rows={rows}
              maxRows={maxRows}
              
              InputLabelProps={{
                sx: isMultiline
                  ? {
                      "&.MuiInputLabel-shrink": {
                        transform: `translate(14px, -${multilineLabelOffset}px) scale(0.75)`,
                        fontSize:`${labelFontSize}px`
                        },
                    }
                  : undefined,
              }}
              slotProps={{
                input: {
                  ...(IconComponent && iconPosition === "start" && {
                    startAdornment: (
                      <InputAdornment position="start">
                        <IconComponent />
                      </InputAdornment>
                    ),
                  }),
                  ...(IconComponent && iconPosition === "end" && {
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconComponent />
                      </InputAdornment>
                    ),
                  }),
                  sx: {
                    color: textColor,
                    backgroundColor: bgColor,
                    fontSize: `${fontSize}px`,
                    ...(isMultiline && {
                      paddingTop: size === "small" ? "18px" : "22px", // Add extra top padding for multiline
                    }),
                  },
                },
                htmlInput: {
                  sx: {
                    color: textColor,
                    ...(isMultiline && {
                      padding: "0 0 8px 0", // Fine-tune textarea inner padding
                    }),
                  },
                },
              }}
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': {
                    borderColor: borderColor,
                  },
                  '&:hover fieldset': {
                    borderColor: borderColor,
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: borderColor,
                  },
                  ...(isMultiline && {
                    '&.MuiInputBase-multiline': {
                      padding: "0 14px", // Ensure consistent multiline padding
                    },
                  }),
                },
              }}
            />
          </>
        )}
      />
    </>
  );
}